<template>
  <div>
    <!--  -->
    <div class="search-header">
      <!--  -->
      <van-icon name="arrow-left" color="white" size="0.48rem" class="goback" @click="$router.back()"/>
      <!--  -->
      <van-search
        v-fofo
        v-model="kw"
        placeholder=""
        background="#007BFF"
        shape="round"
        @input="inputFn"
        @search="enterFn"
      />
    </div>

    <!--  -->
    <div class="sugg-list" v-if="kw.length !== 0">
      <div
        class="sugg-item"
        v-for="(str, index) in suggestList"
        :key="index"
        v-html="lightFn(str, kw)"
        @click="clickFn(str)"
      ></div>
    </div>

    <!--  -->
    <div class="search-history" v-else>
      <!--  -->
      <van-cell title="">
        <!--  right-icon  -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="clearFn"/>
        </template>
      </van-cell>

      <!--  -->
      <div class="history-list">
        <span
          class="history-item"
          v-for="(str, index) in history"
          :key="index"
          @click="hisClickFn(str)"
          >{{ str }}</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import { suggestListAPI } from '@/api'
import { lightFn } from '@/utils/str'
import { setStorage, getStorage } from '@/utils/storage'
export default {
  name: 'Search',
  data () {
    return {
      kw: '', // 
      timer: null, // , 
      suggestList: [], // 
      history: JSON.parse(getStorage('his')) || [] // 
    }
  },
  methods: {
    lightFn,
    // 
    async inputFn () {
      clearTimeout(this.timer)
      this.timer = setTimeout(async () => {
        if (this.kw.length === 0) return // 
        const res = await suggestListAPI({
          q: this.kw
        })
        this.suggestList = res.data.data.options
      }, 500)
    },
    // 
    enterFn () {
      this.history.push(this.kw) // 
      this.history = Array.from(new Set(this.history))
      setTimeout(() => {
        this.$router.push({
          path: `/search/${this.kw}`
        })
      })
    },
    // 
    clickFn (str) {
      this.history.push(str) // 
      this.history = Array.from(new Set(this.history))
      setTimeout(() => {
        this.$router.push({
          path: `/search/${str}`
        })
      })
    },
    // 
    hisClickFn (str) {
      this.$router.push({
        path: `/search/${str}`
      })
    },
    // 
    clearFn () {
      this.history = [] // watch, 
    }
  },
  watch: {
    history () {
      setStorage('his', JSON.stringify(this.history))
    }
  }
  // this.history.push(this.kw) // 
  // this.history = Array.from(new Set(this.history)) // 
}
</script>

<style scoped lang="less">
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;
  /**/
  .goback {
    padding-left: 14px;
  }
  /**/
  .van-search {
    flex: 1;
  }
}

/* */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

/** */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;
  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
